body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.i {
  display: flex;
  height: 100vh;
}

.i-left {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-right {
  flex: 1 1;
  position: relative;
}

.i-left-wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i-intro {
  font-size: 30px;
  font-weight: 400;
}

.i-name {
  font-size: 60px;
}

.i-title {
  height: 50px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  -webkit-animation: move 10s ease-in-out infinite alternate;
          animation: move 10s ease-in-out infinite alternate;
}

@-webkit-keyframes move {
  /* 25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  } */
  100% {
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }
}

@keyframes move {
  /* 25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  } */
  100% {
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }
}

.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #2f98fb;
  display: flex;
  align-items: center;
}

.i-scroll {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}

.i-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}

.i-bg {
  -webkit-clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
          clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background-color: #2f98fb;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (max-width: 480px) {
  .i {
    flex-direction: column;
  }

  .i-left-wrapper {
  
    padding: 10px;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
  }

  .i-desc{
    /* display: none; */
  }
  
}
* {
  scroll-behavior: smooth;
}
.a {
  height: 100vh;
  display: flex;
  align-items: center;
}

.a-left {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}

.a-right {
  flex: 1 1;
}

.a-img {
  border-radius: 5%;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  width: 60%;
  height: 60%;
  object-fit: cover;
  transition: 0.75s all ease-in-out;
}
.a-img:hover{
  -webkit-transform: scale(0.75);
          transform: scale(0.75);
}
.a-title {
  font-weight: 400;
}

.a-sub {
  margin: 20px 0px;
}

.a-desc {
  font-weight: 300;
}
.a-buts{
  margin-top: 20px;
  display: flex;
  justify-content: flex-start;
}
.a-but{
  border: 3px solid #2f98fb;
  color: black;
  background-color: white;
  width: -webkit-max-content;
  width: max-content;
  transition: all 0.75s;
}
.a-but:hover{
  color: white;
  background-color: #2f98fb;
}


@media screen and (max-width: 480px) {
  .a {
    height: auto;
    flex-direction: column;
    text-align: center;
    margin-top: 50px;
  }

  .a-left {
    width: 100%;
  }

  .a-card {
    height: 30vh;
  }

  .a-card.bg,
  .a-award {
    display: none;
  }

  .a-right {
    padding: 20px;
  }
  .a-buts{
    justify-content: center;
  }
}

.c {
  height: 100vh;
  position: relative;
}

.c-bg {
  width: 20px;
  height: 100%;
  background-color: #2f98fb;
  position: absolute;
}

.c-wrapper {
  padding: 50px;
  display: flex;
}

.c-left {
  flex: 1 1;
}

.c-right {
  padding-top: 35px;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
}

.c-title {
  font-size: 55px;
  width: 80%;
}

.c-info-item {
  display: flex;
  align-items: center;
  margin: 50px 0px;
  font-weight: 300;
  width: 70%;
}
.info-img{
  margin-right: 5px;
}
.c-icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.c-desc {
  font-size: 23px;
  font-weight: 200;
}

form {
  margin-top: 20px;
  /* font-size: 20px; */
}

input {
  /* font-size: 20px; */
  width: 50%;
  height: 50px;
  border: none;
  border-bottom: 1px solid black;
  margin: 10px 0px;
  font-size: 17px;
  padding-left: 10px;
}

textarea {
  /* font-size: 20px; */
  width: 100%;
  margin: 10px 0px;
  font-size: 17px;
  padding-left: 10px;
}

button {
  font-size: 17px;
  border-radius: 5%;
  border: none;
  padding: 15px;
  background-color: #2f98fb;
  color: white;
  font-weight: 500;
  cursor: pointer;
  transition: 0.5s all;
}
button:hover{
  background-color: #0084ff;
}

@media screen and (max-width: 480px) {
  .c-wrapper {
    flex-direction: column;
    padding: 0px 50px;
    padding-top: 35px;
  }

  .c-title {
    font-size: 30px;
  }

  .c-info-item {
    margin: 20px 0px;
    width: 100%;
  }

  .c-desc {
    display: none;
  }
  .c-right{
    padding-top: 20px;
  }

  form {
    /* font-size: 200px; */
    margin-top: 0px;
    display: flex;
    flex-wrap: wrap;
  }

  input {
    font-size: 14px;
    width: 35%;
    height: 40px;
    margin: 10px;
    margin-left: 0;
  }
  textarea{
    font-size: 14px;
  }
  button {
    font-size: 14px;
    margin-top: 10px;
  }
}
.f{
    padding: 1rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: #2f98fb;
}
.name{
    color: white;
    padding: 0.5rem;
    font-weight: 300;
}
.icons{
    display: flex;
    justify-content: center;
    padding-bottom: 0.3rem;
}

.f-item{
    padding: 0 0.2rem;
}
.f-bottom{
    display: flex;
    justify-content: center;
}

.f-icon{
    padding: 0 0.5rem;
}

.f-text{
    font-size: 18px;
}

.f-text>a{
    color: black;
    text-decoration: none;
}

@media screen and (max-width:480px){
    .name{
        font-size: 25px;
    }
    .f-text{
        font-size: 15px;
    }
}
.p {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 242);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}

.p-browser {
  height: 20px;
  background-color: rgb(243, 242, 242);
  display: flex;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  z-index: 2;
}

.p-circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: white;
}

.p-img {
  width: 100%;
  transition: all 10s ease;
}

.p:hover .p-img {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

@media screen and (max-width: 480px) {
  .p {
    width: 40%;
    height: 20vh;
  }
}
.pl {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.pl-texts {
  width: 65%;
}

.pl-title {
  font-size: 50px;
  font-weight: 600;
}

.pl-desc {
  margin: 20px 0px;
}

.pl-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media screen and (max-width: 480px) {
  .pl {
    padding: 10px;
  }

  .pl-texts {
    width: 100%;
  }

  .pl-desc {
    /* display: none; */
  }
}
